<template>
  <div class="panel panel-order-list">
    <div class="panel-title">
      <el-form label-position="top" :inline="true">
        <el-form-item label="关键字查询">
          <el-input v-model="key" placeholder="订单编号、姓名、手机号查询" clearable></el-input>
        </el-form-item>
        <el-form-item label="订单时间">
          <el-date-picker v-model="dates" type="daterange" align="right" unlink-panels range-separator="至" format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="交易状态">
          <el-select v-model="status" placeholder="请选择" clearable>
            <el-option v-for="item in statusList" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="hidden-label" label="查询按钮">
          <el-button type="warning" @click.stop.prevent="queryList">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="panel-body">
      <p class="flex-wrap"><span class="mid-flex btn-pass" @click.stop="auditFunc('600501')">确认首付已收</span> <span class="mid-flex btn-refause" @click.stop="auditFunc('600502')">拒绝</span> </p>
      <el-table :data="orderList" cell-class-name="cell-container" header-cell-class-name="cell-container" header-row-class-name="table-header" @selection-change="getSelections">
        <el-table-column type="selection" width="35" align="center">
        </el-table-column>
        <el-table-column prop="orderNo" label="订单编号" width="130" align="center">
        </el-table-column>
        <el-table-column prop="username" label="姓名" width="70" align="center">
        </el-table-column>
        <el-table-column prop="mobile" label="手机号" width="100" align="center">
        </el-table-column>
        <el-table-column prop="productName" label="课程名称" align="center" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="orderType" label="订单类型" width="80" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.orderType | orderType}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="amount" label="总金额（元）" width="95" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.amount / 100}}</span>
          </template>
        </el-table-column>
        <el-table-column label="首付金额（元）" width="105" align="center">
          <template slot-scope="scope">
            <div :contenteditable="scope.row.isEdit" class="edit-input">{{(scope.row.downPayment / 100).toFixed(2)}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="monthlyMortgage" label="每期 应扣金额" width="70" label-class-name="br">
          <template slot-scope="scope">
            <span>{{(scope.row.monthlyMortgage / 100).toFixed(2)}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="periods" label="期数" width="60" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.periods}} 期</span>
          </template>
        </el-table-column>
        <el-table-column prop="orderTime" label="订单时间" width="150" align="center">
        </el-table-column>
        <el-table-column prop="orderStatus" label="状态" width="80" align="center">
          <template slot-scope="scope">
            <span>{{scope.row.orderStatus | orderStatus}}</span>
          </template>
        </el-table-column>
        <el-table-column prop="date" label="操作" width="130" align="center">
          <template slot-scope="scope">
            <router-link :to="`/order-detail/${scope.row.id}`" class="btn-view">查看</router-link>
            <a href="" @click.stop.prevent="changeContent(scope.row, scope.$index)" class="btn-change" v-if="scope.row.orderStatus === '100200' && scope.row.downPayment === scope.row.curDownPayment">修改</a>
            <a href="" @click.stop.prevent="saveAmount(scope.row, scope.$index)" class="btn-save" v-if="scope.row.orderStatus === '100200' && scope.row.downPayment !== scope.row.curDownPayment">完成</a>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page" :page-sizes="[10,20,50,100]" :page-size="size" layout="total, sizes, prev, pager, next" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getOrderStatusList } from '@/assets/js/fields.js';
import { getOrderList, updateOrder, auditOrder } from '@/api/index.js';
import dayjs from 'dayjs';

export default {
  name: 'order-list',
  data() {
    return {
      key: '',
      dates: [
        dayjs()
          .add(-6, 'day')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD'),
      ],
      status: '',
      statusList: getOrderStatusList(),
      orderList: [],
      page: 1,
      size: 10,
      total: 1,
    };
  },
  created() {
    this.orderIds = [];
    this.queryList();
  },
  methods: {
    changeContent(item, i) {
      if (item.orderStatus !== '100200') {
        return;
      }
      item.isEdit = 'plaintext-only';
      this.$nextTick(() => {
        const elem = this.$el.querySelectorAll('.edit-input')[i];
        elem.focus();
        elem.addEventListener('blur', (e) => {
          const value = Number.parseFloat(e.target.innerText);
          item.curDownPayment = value;
        });
      });
    },
    queryList() {
      this.dates = this.dates || [];
      getOrderList(
        this.page,
        this.size,
        this.key,
        this.status,
        this.dates[0],
        this.dates[1],
      )
        .then((result) => {
          this.total = (result && result.total) || 0;
          if (result && Array.isArray(result.list)) {
            this.orderList = result.list.map((item) => {
              item.curDownPayment = item.downPayment;
              item.isEdit = false;
              return item;
            });
          } else {
            this.orderList = [];
          }
        })
        .catch(err => console.error(err));
    },
    saveAmount(item) {
      if (
        Number.parseFloat(item.downPayment)
        !== Number.parseFloat(item.curDownPayment)
      ) {
        updateOrder(item.id, Number(item.curDownPayment * 100))
          .then(() => {
            this.$message.success('更新成功');
            this.queryList();
          })
          .catch(err => console.error(err));
      }
    },
    getSelections(selection) {
      this.orderIds = selection.map(item => String(item.id));
    },
    auditFunc(operation) {
      if (this.orderIds.length === 0) {
        this.$message.warning('请选择表格项');
        return;
      }
      auditOrder(this.orderIds, operation)
        .then(() => {
          this.$message.success('审核成功');
          this.queryList();
        })
        .catch(console.error);
    },
    handleSizeChange(val) {
      this.size = val;
      this.page = 1;
      this.queryList();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.queryList();
    },
  },
};
</script>

