      background: #f0f3fa;
    }
    .el-form-item:last-child {
      margin-right: 0;
    }
    .el-button {
      width: 120px;
      background: #f88311;
    }
  }
}
.panel-body {
  > p {
    height: 64px;
    justify-content: flex-end;
    align-items: center;
    padding-right: $p30;
    span {
      cursor: pointer;
      width: 110px;
      height: 30px;
      background: rgba(65, 127, 213, 1);
      border-radius: 4px;
      font-size: 14px;
      color: rgba(255, 255, 255, 1);
    }
    .btn-refause {
      margin-left: 10px;
      width: 80px;
      background: rgba(255, 113, 104, 1);
    }
  }
  .el-table {
    box-sizing: border-box;
    margin: 0 $p30;
    width: auto;
  }
  .btn-view,
  .btn-change,
  .btn-save {
    border-radius: 4px;
    border: 1px solid;
    padding: 2px 10px;
    font-size: 14px;
    color: $txtColor;
    line-height: 14px;
    margin-right: 10px;
    text-decoration: none;
  }
  .btn-save {
    background: $txtColor;
    color: #fff;
  }
  .edit-input {
    &:focus {
      background: rgba(240, 243, 250, 1);
      border-radius: 1px;
      outline: 1px solid rgba(134, 152, 176, 1);
      outline-offset: -1px;
    }
  }
  .el-pagination {
    margin: 20px 15px;
  }
}
</style>
